/*
html5doctor.com Reset Stylesheet
v1.6.1
Last Updated: 2010-09-17
Author: Richard Clark - http://richclarkdesign.com
Twitter: @rich_clark
*/
* {
  box-sizing: border-box; }

html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent; }

body {
  line-height: 1; }

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

ul, nav ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent;
  text-decoration: none; }

/* change colours to suit your needs */
ins {
  background-color: #ff9;
  color: #000;
  text-decoration: none; }

/* change colours to suit your needs */
mark {
  background-color: #ff9;
  color: #000;
  font-style: italic;
  font-weight: bold; }

del {
  text-decoration: line-through; }

abbr[title], dfn[title] {
  border-bottom: 1px dotted;
  cursor: help; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

/* change border colour to suit your needs */
hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #cccccc;
  margin: 1em 0;
  padding: 0; }

input, select {
  vertical-align: middle; }

/* _-_-_- base/BASE -_-_-_ */
body {
  background: white;
  color: #2D2D2D;
  font-family: "Fira Sans", sans-serif;
  line-height: 1.4; }

a {
  color: #754C68;
  cursor: pointer; }

.clearfix {
  overflow: auto;
  zoom: 1; }

/* _-_-_- base/TYPOGRAPHY -_-_-_ */
#mainBanner {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 8pt;
  background: rgba(255, 255, 255, 0.25); }
  #mainBanner img {
    user-drag: none; }
  #mainBanner #logoBanner {
    display: block;
    width: 400pt;
    max-width: 80vw;
    margin: 8pt auto; }
  #mainBanner h2 {
    width: 560pt;
    max-width: 90vw;
    text-align: center;
    color: white;
    font-size: 1.6rem; }
  #mainBanner a {
    display: block;
    width: 140pt;
    margin: 8pt auto;
    padding: 8pt 16pt;
    text-align: center;
    font-weight: bold;
    color: #754C68;
    border: 3px solid #754C68;
    background: rgba(194, 193, 188, 0.5);
    white-space: nowrap; }

.downloads {
  display: flex;
  flex-flow: row wrap;
  justify-content: center; }

/* _-_-_- modules/BUTTONS -_-_-_ */
button {
  outline: none;
  border: none;
  cursor: pointer; }

input[type=text] {
  outline-width: 2px;
  transition: outline .1s ease-in; }
  input[type=text]:valid {
    outline-color: #00cd00; }
  input[type=text]:invalid {
    outline-color: #ff0a0a; }

#featureBoxContainer {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  margin-top: 16pt; }

.featureBox {
  width: 240pt;
  margin: 8pt;
  background-color: white;
  transition: 0.2s linear; }
  .featureBox .center {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 80pt;
    margin-bottom: 0.5em;
    padding: 4pt 0;
    background: rgba(253, 223, 117, 0.75); }
    .featureBox .center svg {
      width: auto;
      height: 24pt; }
      .featureBox .center svg path {
        fill: #2D2D2D; }
  .featureBox .desc {
    padding: 1em; }
  .featureBox h2 {
    margin-bottom: 0.25em;
    font-weight: bold;
    font-size: 1.6rem; }
  .featureBox p {
    color: #6c6c6c; }

