/* Style.css */
body {
  margin: 0;
  padding: 0; }

#smallScreenNav {
  display: none;
  width: 100%;
  height: 100vh;
  position: fixed;
  background-color: rgba(55, 55, 55, 0.75);
  justify-content: center;
  align-items: center; }

#smallScreenNav li {
  list-style: none;
  font-size: 40px;
  text-align: center;
  font-family: sans-serif;
  margin: 10px 0; }

.hamburger {
  display: none;
  position: fixed;
  top: 25px;
  right: 20px;
  color: white;
  z-index: 100; }

.scrolled .hamburger {
  color: #373737; }

.nav {
  width: 100vw;
  padding: 15px 0px;
  position: fixed;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  font-size: 16px;
  color: white;
  font-family: sans-serif;
  background-color: transparent;
  transition: 0.7s;
  z-index: 5; }
  .nav li {
    list-style: none;
    display: inline;
    margin: 0 20px;
    position: relative; }
    .nav li:hover {
      text-decoration: underline; }
  .nav img {
    width: 55px;
    visibility: hidden; }

.navLogoContainer {
  position: fixed;
  display: flex;
  height: 85px;
  align-items: center;
  margin-left: 20px;
  z-index: 15;
  color: white; }

.navLogoContainer img {
  width: 50px;
  transition: 0.7s; }

.scrolled img {
  transition: 0.7s; }

.navLogoContainer p {
  margin-left: 10px;
  font-size: 20px;
  font-style: italic;
  font-weight: 600;
  letter-spacing: 1px;
  font-family: sans-serif; }

.scrolled {
  color: black;
  transition: 0.7s; }

.nav.scrolled {
  background: white;
  box-shadow: 0 5px 5px -5px #333; }

li > a {
  position: relative;
  color: white;
  text-decoration: none; }

li > a:hover {
  color: white; }

.scrolled li > a {
  color: black; }

.scrolled li > a:hover {
  color: black; }

.scrolled li > a:before {
  background-color: black; }

li > a:hover:before {
  visibility: visible;
  -webkit-transform: scaleX(1);
  transform: scaleX(1); }

.active a {
  background-color: #754C68;
  color: white !important;
  padding: 8pt;
  border-radius: 5px; }

.page em {
  color: #373737;
  margin: 5px 50px; }

.bigBtn {
  padding: 15px 25px;
