/* media queries*/
@media screen and (min-width: 730px) {
  .hamburger {
    display: none; } }

@media screen and (max-width: 1150px) {
  #footer .fa-reddit {
    display: none; } }

@media screen and (max-width: 885px) {
  #footer {
    flex-direction: column;
    padding: 8pt; }
    #footer #socialWrapper {
      margin-bottom: 16pt; }
  #footer2 {
    text-align: center; }
    #footer2 span {
      display: block;
      float: none; } }

@media screen and (max-width: 730px) {
  .nav li {
    display: none; }
  .hamburger {
    display: inline-block; }
  .navLogoContainer {
    top: 0; } }

@media screen and (max-width: 500px) {
  #page1 #mainBanner {
    padding: 96pt 0; }
  #page1 h2 {
    width: calc(100vw - 16pt);
    font-size: 5vw; }
  #page3 .featureBox {
